  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 24px;
  line-height: 1.21;
  font-weight: 900;
  text-align: start;
  margin: 25px 0 25px 21px;
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}